html {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-text {
  color: "darkblue";
}

.reuniones {
  color: bisque;
  font-weight: 600;
}

.clave-label {
  background-color: aquamarine;
  padding: 2px;
  padding-right: 4px;
}

.clave {
  margin-top: 2em;
}

.clave-input {
  width: 14em;
}

.editor-buttons {
  margin-bottom: 1em;
}

.editor-button {
  margin-right: 0.5em;
}
.editor-div {
  background-color: white;
}

.title-input {
  margin-bottom: 1em;
}
.footer-name-span {
  display: inline-block;
  margin-right: 2em;
}

.footer-link {
  display: inline-block;
  text-decoration: none;
}

.footer-link-divider {
  display: inline-block;
  margin-left: 1em;
  margin-right: 1em;
}

@media (max-width: 390px) {
  .footer-link-divider {
    margin-left: 0.5em;
    margin-right: 0.5em;
  }
}
@media (max-width: 335px) {
  .footer-link-divider {
    margin-left: 0.25em;
    margin-right: 0.25em;
  }
}

.teaching-index ul li {
  list-style-type: none;
}

.teaching-index {
  margin-bottom: 2em;
}

.clave-incorrecto {
  color: #ee3127;
  background-color: pink;
  max-width: 15em;
}
